.home-container {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
}

.profile-section {
  position: sticky;
  top: 20px;
  margin-right: 20px;
  flex-shrink: 0;
  width: 150px; /* Adjust sidebar width */
}

.profile-picture {
  width: 100px; /* Smaller profile picture */
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.content-section {
  flex: 1;
  margin-left: 20px;
}

.shelf {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  justify-items: center;
  margin-top: 40px;
}

.shelf-item {
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  width: 200px;
  margin: 20px;
}

.shelf-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 4px;
}

.shelf-item p {
  margin-top: 10px;
  font-size: 1rem;
}

.App-header {
  background-color: #f0f0f0;
  padding: 20px;
  font-size: 1.5rem;
}

.App-header nav a {
  margin: 0 10px;
  text-decoration: none;
  color: blue;
}

.App-header nav a:hover {
  text-decoration: underline;
}

/* Circular Logos */
.logo {
  width: 50px; /* Adjust the size to be a little smaller */
  height: 50px;
  margin: 5px 0;
  border-radius: 50%; /* Make the logos circular */
  object-fit: cover; /* Ensure the logo fits well inside the circle */
}

.links-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.links-container p {
  margin-top: 5px;
  text-align: center;
}

.links-container a {
  display: inline-block;
  margin-bottom: 10px;
}

/* Increase font size for small plain text */
body {
  font-size: 16px; /* Global font size for all text */
}

/* Specifically increase for smaller paragraphs, lists, or non-heading text */
p, li {
  font-size: 1.1rem; /* Increase the size slightly for small plain text */
}

.links-container p, .shelf-item p {
  font-size: 1.05rem; /* Smaller increase for the sidebar links and shelf text */
}
